.card-list{

}
.card-list .card-item{

}
.card-list .card-item h3{
	font-family: 'Hind';
	font-size: 14px;
	white-space: nowrap;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	overflow: hidden;
}
.card-list .card-item h3 a{
	color: #111;
	font-family: 'Hind Bold';
}
.card-list .card-item .image{
	margin: 0;
	margin-right: 15px;
	float: left;
}
.card-list .card-item p{
	font-size: 12px;
	color: #555;
}

/*Profil*/

.card-metadata{

}
.card-metadata .image{
	margin-bottom: 20px;
}

.card-metadata h1{
	font-size: 28px;
	font-family: 'Hind Bold';
	text-align: center;
	margin-bottom: 45px;
}
.card-metadata .metadata{
	font-size: 16px;
	margin-bottom: 10px;
	color: #555;
	font-size: 14px;
	width: 40%;
}
.card-metadata .metadata label{
	font-weight: normal;
	font-family: 'Hind Bold';
	font-size: 14px;
	color: #111;
	margin-right: 10px;
	margin-bottom: 0;
}
.card-metadata .metadata.metadata-right{
	margin-left: 55%;
}

.card-metadata .card-table{
	margin-top: 20px;
	margin-bottom: 60px;
}
.card-metadata .card-table thead tr th{
	font-size: 16px;
	font-weight: normal;
	font-family: 'Hind Bold';
	background-color: #111;
	padding: 10px 15px;
	border-bottom: none;
}
.card-metadata .card-table tbody tr.resume td{
	border-top: 2px solid #bbb;
}
.card-metadata .card-table tbody tr td{
	font-size: 14px;
	font-family: 'Hind';
	font-weight: normal;
	padding: 7px 15px;
}
.card-metadata .card-table tbody tr td.resume{
	font-family: 'Hind Bold';
	font-weight: normal;
}
.card-metadata .card-table tbody tr td.resume-litteral{
	background-color: #fff !important;
	padding-top: 30px;
	padding-left: 0;
}
.card-metadata .card-table tbody tr td b{
	font-family: 'Hind Bold';
	font-weight: normal;
}

.card-metadata .card-footer{
	margin-top: 30px;
	padding-left: 0;
	padding-right: 0;
	background-color: transparent;
	display: -ms-flexbox;
	display: flex;
}


